@use "@utils/variables" as v;

.wrapper {
    width: 100%;
    max-width: 450px;
    display: flex;
    flex-direction: column;
    border-radius: 50px;

    input,
    textarea {
        margin-bottom: 1.5em;
        border: none;
        border-radius: 10px;
        height: 40px;
        padding-left: 1em;
        box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);
    }

    textarea {
        padding-top: 1em;
        height: 100px;
    }

    p {
        color: red;
        font-size: 0.8rem;
        margin: 0;
        margin-bottom: 20px;
        margin-top: -15px;
        padding-left: 5px;
    }

    p.apiError {
        margin-top: 0;
    }

    button {
        border: none;
        cursor: pointer;
        width: 100%;
        height: 40px;
        margin: 0 auto;
        margin-top: 2em;
        background-color: v.$themePurple;
        color: white;
        border-radius: 20px;

        &:disabled {
            background-color: rgba(1, 1, 1, 0.1);
            color: v.$themePurple !important;
        }
    }
}
