@use "@utils/variables" as v;

.wrapper {
    .banner {
        height: 420px;
        width: 100%;
        background-color: #eeeeee;
        background-image: url("../../assets/courses/banner_bg.svg");
        background-position: center;
        background-size: cover;
        overflow-x: hidden;
        overflow-y: hidden;
        position: relative;

        .graphic.bug {
            position: absolute;
            top: 45px;
            right: 160px;
            img {
                width: 35px;
            }
        }

        .star {
            position: absolute;

            img {
                width: 20px;
            }

            &.star1 {
                opacity: 0.5;
                left: 20em;
                top: 7em;
            }

            &.star2 {
                opacity: 0.8;
                left: 40%;
                top: 8em;
            }

            &.star3 {
                opacity: 0.8;
                left: 50%;
                top: 3em;
            }

            &.star4 {
                right: 30em;
                bottom: 10em;
            }
        }

        .lottie.movingClouds {
            position: absolute;
            top: -8em;
            left: 42%;
            transform: translateX(-50%);
        }

        .lottie.redButterfly {
            position: absolute;
            left: 45%;
            bottom: 20%;
            transform: rotate(30deg);
        }

        .lottie.flowerWithFly {
            position: absolute;
            left: 5%;
            bottom: -13px;
            display: flex;

            div.fly {
                position: absolute;
                top: -50px;
                right: -50px;
            }
        }

        .lottie.shiningStars {
            position: absolute;
            left: 50%;
            top: 100px;
            transform: translateX(-50%);
        }

        .lottie.elephant {
            position: absolute;
            left: 10%;
            bottom: -92px;
        }
        .graphic.deer {
            position: absolute;
            left: 35%;
            bottom: -10px;

            img {
                width: 70px;
            }
        }

        .monkeyTrees {
            position: absolute;
            right: 0px;
            bottom: 0;

            .lottie {
                position: absolute;
                right: 0;
                bottom: 0;
            }

            .flowerInTrees {
                right: 250px;
                z-index: 1;
            }

            .treeLeft {
                right: 100px;
                bottom: -104px;
                z-index: 2;
            }
            .treeRight {
                right: -170px;
                bottom: -134px;
                z-index: 2;
            }
            .monkey {
                right: 90px;
                bottom: -10px;
                z-index: 1;
            }
        }
    }

    .tabs {
        display: flex;
        justify-content: space-evenly;
        width: 100%;

        button {
            background-color: v.$lightPurple;
            color: white;
            border: none;
            height: 35px;
            padding-inline: 2em;
            width: 12em;
            cursor: pointer;

            $bRadius: 30px;
            border-bottom-right-radius: $bRadius;
            border-bottom-left-radius: $bRadius;
        }

        button.active {
            background-color: v.$themePurple;
        }

        margin-bottom: 100px;
    }

    .coursesSection {
        width: 100%;
        background-image: url("../../assets/courses/bg.svg");
        background-position: left top;
        background-repeat: repeat-y;
        background-size: cover;
        padding-bottom: 3em;

        .courses {
            width: 100%;
            $imgMargin: 4em;

            .course {
                display: flex;
                align-items: center;
                justify-content: space-between;
                max-width: 100%;
                margin-bottom: 10em;

                .image {
                    width: 40%;
                    min-width: 350px;
                    height: 250px;
                    border: 1px solid v.$themePurple;
                    display: flex;
                    cursor: pointer;
                    margin-right: $imgMargin;
                    border-radius: 100px;
                    box-shadow: 1px 1px 10px v.$themePurple;

                    display: flex;
                    justify-content: center;
                    align-items: center;

                    position: relative;
                    .learnMoreBtn {
                        position: absolute;
                        bottom: -15px;

                        button {
                            width: 120px;
                            height: 35px;
                            border-radius: 50px;
                            background-color: v.$themePurple;
                            border: none;
                            color: white;
                            cursor: pointer;
                        }
                    }

                    img {
                        max-width: 400px;
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                        border-radius: 50px;
                    }
                }

                .content {
                    width: 60%;

                    .title {
                        color: v.$themePurple;
                    }

                    .desc {
                        line-height: 30px;
                        color: #999999;
                        margin-bottom: 2em;
                    }
                }
            }

            .course.alt {
                margin-left: auto;
                margin-left: 1em;

                .image {
                    order: 2;
                    margin-right: 0em;
                    margin-left: $imgMargin;
                }
            }
        }
    }

    .learnSection {
        padding-top: 2em;
        margin-bottom: 8em;

        header {
            margin-bottom: 4em;
        }

        .cards {
            display: flex;
            justify-content: space-between;

            article {
                display: flex;
                flex-direction: column;
                justify-content: center;

                .icon {
                    background-color: #f4fdff;
                    border-radius: 90px;
                    width: 220px;
                    height: 220px;
                    margin-bottom: 2em;
                    box-shadow: 1px 1px 5px rgba(1, 1, 1, 0.1);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }

                .title {
                    text-align: center;
                    color: v.$themePurple;
                    font-weight: 500;
                    font-size: 1.25rem;
                }
            }
        }
    }
}

@media screen and (max-width: 1000px) {
    .wrapper {
        .banner {
            .graphic.deer {
                display: none;
            }

            .graphic.star {
                display: none;
            }

            .monkeyTrees {
                right: -30px !important;

                .lottie {
                    position: absolute;
                    right: 0;
                    bottom: 0;
                }

                .flowerInTrees {
                    display: none;
                }

                .treeLeft {
                    right: 100px;
                    bottom: -104px;
                    z-index: 2;
                }
                .treeRight {
                    right: -130px;
                    bottom: -123px;
                    z-index: 2;
                }
                .monkey {
                    right: 100px;
                    bottom: -25px;
                    z-index: 1;
                }
            }
        }

        .coursesSection {
            .courses {
                .course {
                    max-width: 100%;
                    flex-direction: column;
                    align-items: center;
                    margin-bottom: 5em;

                    .image {
                        margin-bottom: 2em;
                        margin-right: 0;
                    }

                    .content {
                        width: 100%;

                        .button {
                            display: flex;
                            width: 100%;
                            justify-content: center;

                            button {
                                margin: 0 auto !important;
                            }
                        }

                        .desc {
                            text-align: center;
                            margin-bottom: 1em;
                        }
                    }
                }

                .course.alt {
                    margin-left: auto;
                    margin-left: 1em;
                    .image {
                        order: 1;
                        margin-right: 0em;
                        margin-left: 4px;
                    }
                    .content {
                        order: 2;
                    }
                }
            }
        }

        .learnSection {
            padding-top: 5em;

            header {
                margin-bottom: 4em;
            }

            .cards {
                display: flex;
                flex-direction: column;

                article {
                    display: flex;
                    flex-direction: column;
                    justify-content: unset;
                    align-items: center;
                    margin-bottom: 2em;
                }
            }
        }
    }
}

@media screen and (max-width: 800px) {
    .wrapper {
        .banner {
            .lottie.elephant {
                display: none;
            }

            .monkeyTrees {
                right: 50% !important;
                transform: translateX(50%) !important;
                width: 250px;

                .treeLeft {
                    right: unset;
                    left: -160px;
                    // border: 1px solid red;
                }
                .treeRight {
                    left: unset;
                    right: -220px;
                }
                .monkey {
                    right: 20px;
                    bottom: -25px;
                    z-index: 1;
                }
            }

            .lottie.flowerWithFly {
                display: none;
            }

            .lottie.redButterfly {
                display: none;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        .banner {
            height: 350px;
        }

        .learnSection {
            header {
                text-align: center;
            }
        }

        .tabs {
            button {
                width: fit-content;
                padding-inline: 1.8em;
                height: 40px;
                margin-right: 10px;
            }
        }
    }
}
