@use "@utils/variables" as v;

.wrapper {
    width: 520px;
    height: 310px;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    .lottie {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
    }

    overflow: hidden;

    .graphic {
        position: absolute;
        z-index: 10;

        &.bgTop {
            position: absolute;
            top: -7px;
            right: -7px;
            border-top-right-radius: 20px;
        }

        &.bgBottom {
            position: absolute;
            left: -10px;
            bottom: -15px;

            img {
                width: 180px;
            }
        }
    }

    h1 {
        text-align: center;
        margin-bottom: 1em;
        color: v.$themePurple;
    }

    div {
        text-align: center;
        line-height: 30px;
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        width: 95vw;
    }
}
