@use "@utils/variables" as v;

.wrapper {
    max-width: 800px;

    padding: 2em;
    max-height: 680px;
    height: fit-content;
    overflow-y: auto;

    .course {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        .top {
            margin-bottom: 1.5em;
            width: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;

            .title {
                font-size: 1.2rem;
                color: v.$themePurple;
                font-weight: 600;
                margin-bottom: 1.5em;
                text-align: center;
            }

            .image {
                min-width: 350px;
                max-width: 400px;
                height: 250px;
                display: flex;
                align-items: center;
                justify-content: center;

                border: 1px solid red;
                border-radius: 100px;
                box-shadow: 1px 1px 10px v.$lightPurple;
                margin-bottom: 2em;

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50px;
                    object-fit: cover;
                }
            }

            .info {
                width: 100%;
                .inner {
                    display: flex;
                    justify-content: space-between;
                    width: 100%;

                    div {
                        display: flex;
                        align-items: center;
                        color: rgba(1, 1, 1, 0.5);

                        svg {
                            color: rgba(1, 1, 1, 0.5);
                            margin-right: 0.2em;
                        }
                    }

                    .fee {
                        font-weight: 600;
                        font-size: 1.3rem;
                        color: v.$themePurple;
                    }
                }
            }
        }

        .content {
            width: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;

            .title {
                text-align: center;
                color: v.$themePurple;
            }

            .desc {
                text-align: center;
                line-height: 30px;
                color: #999999;
                margin-bottom: 2em;
            }

            button {
            }
        }
    }
}

@media screen and (max-width: 850px) {
    .wrapper {
        max-width: 95vw;
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        padding: 1em;

        .course {
            .top {
                .info {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .inner {
                        flex-direction: column;
                        width: 150px;

                        div {
                            width: 100%;
                            justify-content: center;
                            margin-bottom: 10px;

                            span,
                            svg {
                                margin-right: 10px;
                            }

                            svg {
                                $size: 30px;
                                width: $size;
                                height: $size;
                            }
                        }
                    }
                }
            }
            .content {
                width: 100%;
                .desc {
                    text-align: left;
                }
            }
        }
    }
}

@media screen and (max-width: 400px) {
    .wrapper {
        .course {
            .top {
                .image {
                    min-width: unset;
                    width: 100%;
                }
            }
        }
    }
}
